.Home {
    width: 1300px;
    margin-left: auto;
    margin-right: auto;
    background: #ffccbc;
    border-radius: 10px;
    margin-top: 20px;
}

.content{
    background-color: #FFB79A;
    width: auto;
    height: auto;
    padding: 40px;

}

.controls{
    width: 50%;
    height: auto;
    /*background-color: green;*/
    float: left;
}

.simulator{
    width: 50%;
    height: auto;
    /*background-color: blue;*/
    float: left;
}

.commands{
    width: 100%;
    height: auto;
    /*background-color: red;*/
    clear: both;

}

footer{
    background: #FFF6F3;
    border: 1px solid #FFB79A;
}

footer p {
    color: #FF8858;
}

@-webkit-keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


