.simulatorPanel-content{
    padding: 80px 50px;
}

.flex-grid { /* Safari */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;


}
.col {  /* Safari 6.1+ */
    -ms-flex: 1;  /* IE 10 */
    flex: 1 1;

    /* margin: 10px; */

}

.ladyBug{
    background-image: url("../images/ladybird.png") ;
    width: 61px;
    height: 61px;
    background-size: contain;

    background-color: white;
    border: 1px solid #000;
}

.ladyBug90{
    background-image: url("../images/ladybird.png") ;
    width: 61px;
    height: 61px;
    background-size: contain;

    background-color: white;
    border: 1px solid #000;
    -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
            transform: rotate(90deg);
}
.ladyBug180{
    background-image: url("../images/ladybird.png") ;
    width: 61px;
    height: 61px;
    background-size: contain;

    background-color: white;
    border: 1px solid #000;
    -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
            transform: rotate(180deg);
}
.ladyBug270{
    background-image: url("../images/ladybird.png") ;
    width: 61px;
    height: 61px;
    background-size: contain;

    background-color: white;
    border: 1px solid #000;
    -webkit-transform: rotate(270deg);
        -ms-transform: rotate(270deg);
            transform: rotate(270deg);
}



